import type { LitElement } from 'lit';
import { documentElementClipController } from './document-element-clip-controller.js';
type Constructor<T = object> = new (...args: any[]) => T;
declare class DocumentElementClipMixinInterface {}

export const DocumentElementClipMixin = <T extends Constructor<LitElement>>(superClass: T) => {
  // @ts-ignore
  class DocumentElementClipEl extends superClass {
    __docElClipRefCount: number;
    constructor() {
      super();
      this.__docElClipRefCount = 0;
    }

    disconnectedCallback() {
      documentElementClipController.unclip();
    }

    _clipDocumentElement() {
      documentElementClipController.clip();
      this.__docElClipRefCount += 1;
    }

    _unclipDocumentElement() {
      const prevDocElClipRefCount = this.__docElClipRefCount;
      this.__docElClipRefCount = Math.max(0, this.__docElClipRefCount - 1);

      if (this.__docElClipRefCount === prevDocElClipRefCount) return;
      documentElementClipController.unclip();
    }
  }
  return DocumentElementClipEl as Constructor<DocumentElementClipMixinInterface> & T;
};
